@charset "utf-8";



body{
    margin: 0px;
    padding: 0px;
    font-family: poppins;
   
}

*{
    box-sizing: border-box;
    scroll-behavior: smooth;  
}

ul{
    list-style: none;
}
a{
    text-decoration: none;
}

#main{
    width: 100%;
    height: 100vh;
    position: relative;
}

nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 5px 10px 50px rgb(0, 0,0,0.02);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: #ffffff;
}

.logo img{
    height: 45px;
}

nav ul{
    display: flex;
}

nav ul li a{
    height: 40px;
    line-height: 43px;
    margin: 3px;
    padding: 0px 22px;
    display: flex;
    font-size: 0.8rem;
    text-transform: uppercase;
    font-weight: 500;
    color: #585b5b;
    letter-spacing: 1px;
}

.hey{
    color: #39bfbd;
    font-weight: 500;
    font-size: 0.9rem;
    border-bottom: 2px solid #40c6c4;
}

nav ul li a:hover{
    background-color: rgb(211, 211, 98);
    /* background-color: #2adabc; */
    color: #FFFFFF;
    box-shadow: 5px 10px 30px rgba(64, 198, 196, 0.3);
    transition: all ease 0.2s;
    border-radius: 40%;
}

.model img{
    width: 100%;
    height:100% ;
    object-fit: contain;
    border-radius: 50%;
}

.model{
    width: 500px;
    height: 500px;
}

.content{
    width: 90%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.main-text{
    width: 500px;
}

.main-text h1{
    font-size: 3.5rem;
    color: #1c3548;
    margin: 0px 0px 10px 0px;
    line-height: 60px;
    font-family: arial;
    
}

.main-text p{
    color: #747474;
    font-family: arial;
}

.portfolio-btn,
.recent-btn,
.client-box a{
    width:150px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    background-color: rgb(207, 207, 17);
    /* background-color: #1db096; */
    border-radius: 20px;
    box-shadow: 4px 10px 30px rgba(24, 139, 119, 0.2);
}

.portfolio-btn:hover,
.recent-btn:hover,
.client-box a:hover,
.contact-input a:hover{
    background-color: #23cdaf;
    transition: all ease 0.2s;
}

#skills{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    margin: auto auto 50px auto;
}

.skill-text{
    width: 400px;
}

.skill-img{
    width: 600px;
    height: 600px;
}

.skill-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.skill-heading span{
    color: #36b7b5;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1rem;
    font-family: arial;
}

.skill-heading h2{
    color: #1c3548;
    font-size: 3.3rem;
    margin: 0px;
    font-weight: 400;
    letter-spacing: 1px;
    font-family: arial;
}

.s-box-container{
    margin-top: 30px;
}

.skill-box{
    display: flex;
    margin: 25px 0px;
    font-family: arial;
}

.s-box-icon{
    margin-right: 12px;
}


.s-box-text-strong{
    color: #1c3548;
    margin: 0px;
    font-size: 0.9rem;
    font-weight: 600;
    
}

.s-box-text-p{
    color: #747474;
    font-size: 0.9rem;
    letter-spacing: 1px;
   
}

#recent{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px 0px;
    flex-direction: column;
    font-family: arial;
}

.recent-heading span,
.client-heading{
    color: #36b7b5;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1.1rem;
}

.recent-heading h3,
.client-heading h3,
#contact h3{
    color: #1c3548;
    font-size: 3rem;
    margin: 0px;
    font-weight: 400;
    letter-spacing: 1px;
}

.recent-img{
    margin: 30px 40px;
}

.recent-img img{
    width: 100%;
    height: 100%;
}

.recent-btn{
    width: 150px;
    margin-top: 10px;
}


/* ******************* */

#client{
    width: 100%;
    border-top: 1px solid rgb(58, 58, 58, 0.03);
    border-bottom: 1px solid rgba(58, 58, 58, 0.03);
    padding: 50px 0px;
    /* background-image: ; 00000000000000000000000000000000000000000000*/
    background-position: center;
    background-size: 1000px;
}

.client-heading{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


/* ************************** */

.c-box-container{
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px 0px;
}

.client-box{
    width: 310px;
    height: 350px;
    background-color: rgb(219, 214, 214);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0px;
    margin: 20px;
    box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.08);
}

.client-box img{
    width: 90px;
    height: 90px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 20px;
}

.client-box strong{
    color: #1c3548;
    margin: 0px;
    font-size: 1.1rem;
    font-weight: 600;
    margin-top: 10px;
}

.star{
    display: flex;
    margin: 10px 0px 10px 0px;
}

.star i{
    color: #ff9529;
}

.client-box p{
   
    font-size: 0.9rem;
    margin: 0px;
    text-align: center;
    display: block;
    display: -webkit-box;
    max-width: 80%;
}

.client-box a{
    width: 140px;
    margin-top: 20px;
    border-radius: 0px;
}

#contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 40px 0px;
}

#contact h3{
    font-size: 2.5rem;
    margin: 20px;
}

#contact img{
    height: 300px;
}

.contact-input{
    width: 400px;
    height: 50px;
    background-color: #FFFFFF;
    display: flex;
    justify-content: center;
    border-radius: 50px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
}

.contact-input input{
    width: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    text-align: center;
    color: #242425;
}

.contact-input a{
    width: 200px;
    height: 35px;
    background-color: #1db01d;
    color: #FFFFFF;
    display: flex;
    font-size: 0.9rem;
    justify-content: center;
    align-items: center;
    margin: auto 10px;
    border-radius: 20px;
    font-weight: 500;
}

footer{
    display: flex;
    justify-content: space-around;
    border-top: 1px solid rgba(232,232,232,0.5);
    background-color: skyblue;
}

footer p{
    margin: 15px 0px;
    color: #5f5f5f;
    font-size: 0.9rem;
}
footer img{
    margin-top: 2%;
    width: 20px;
    height: 20px;
    

}
nav .menu-btn{
    display: none;
}

@media(max-width:1140px){
    .content{
        width: 100%;
    }
    .model{
        width: 400px;
        height: 400px;
    }
    .main-text,.model{
        margin: 10px;
    }
    .main-text h1{
        font-size: 3rem;
    }

    .skill-img{
        width: 400px;
        height: 400px;
    }
    .client-box{
        flex-grow: 0.5;
    }
}

@media(max-width:900px){
    nav{
        justify-content: space-between;
        height: 65px;
        padding: 0px 30px;
    }
    .logo img{
        height: 35px;
    }
    .menu{
        display:none;
        position: absolute;
        top: 65px;
        left: 0px;
        background-color: #FFFFFF;
        border-bottom: 4px solid #1db096;
        width: 100%;
        padding: 0px;
        margin: 0px;
    }
    .menu li{
        width: 100%;
    }

    nav .menu li a{
        width: 100%;
        height: 40px;
        justify-content: center;
        align-items: center;
        margin: 0px;
        padding: 25px;
        border: 1px solid rgba(38, 38, 38, 0.03);
    }
    nav .menu-icon{
        cursor: pointer;
        float: right;
        padding: 28px 20px;
        padding: relative;
        user-select: none;
    }

    nav .menu-icon .nav-icon{
        background-color: #333333;
        display: block;
        height: 2px;
        position: relative;
       
        width: 18px;
    }
    nav .menu-icon .nav-icon::before,
    nav .menu-icon .nav-icon::after{
        background: #333333;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        transition: all ease-out 0.2s;
        width: 100%;
    }

    nav .menu-icon .nav-icon::before{
        top: 5px;
    }
    nav .menu-icon .nav-icon::after{
        top: -5px;
    }
    nav.menu-btn:checked ~ .menu-icon .nav-icon{
        background: transparent;
    }
    nav .menu-btn{
        display: none;
    }
    nav .menu-btn:checked ~ .menu{
        display: block;
    }
    .skills{
        width: 100%;
        justify-content: space-between;
        padding: 40px;
    }
    .skill-text{
        width: 400px;
    }
    .skill-img{
        width: 40px;
        height: 300px;
    }
    .skill-img img{
        object-fit: cover;
        object-position: left;
    }
    .recent-heading h3,
    .client-heading h3,
    #contact{
        font-size: 2.2rem;
        text-align: center;
    }
    .recent-heading{
        text-align: center;
        padding: 0px 15px;
    }
}

@media(max-width:720px){
    .model{
        display: none;
    }
    .main-text{
        width: 70%;
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    #contact img{
        height: 200px;
    }
    footer{
        justify-content: center;
        padding: 10px;
    }
    footer p{
        text-align: center;
    }
    .recent-btn{
        margin: 0px;
    }
}

@media(max-width:530px){
    .content-input{
        width:90%
    }
    .skill-img{
        display: none;
    }
    .skill-text{
        width: 85%;
    }
    #skills{
        margin: 0px;
    }
}


